import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Card, Stack, Typography, Button, makeStyles } from '@mui/material';

const useStyles = makeStyles({
    suggestionImage: {
        width: '60%',
        height: '60%',
    },
});

export default function StationRecoCard({ suggestionImageSrc, suggestionSubheader, sx, ...other }) {
    const [isSubheaderVisible, setIsSubheaderVisible] = useState(false);
    const classes = useStyles();

    const handleImageClick = () => {
        setIsSubheaderVisible(!isSubheaderVisible);
    };

    return (
        <Card sx={{ px: 4, py: 4, borderRadius: 10, ...sx }} {...other}>
            <Stack direction="row" alignItems="center" spacing={2}>
                <Button
                    variant="outlined"
                    sx={{
                        p: 0,
                        borderColor: '#8cacff',
                        width: '4rem',
                        height: '4rem',
                        '&:hover': {
                            borderColor: '#6b8cff',
                        },
                    }}
                    onClick={handleImageClick}
                >
                    <img src={suggestionImageSrc} alt="Suggestion Image" className={classes.suggestionImage} />
                </Button>
                {isSubheaderVisible && (
                    <Typography
                        variant="subtitle1"
                        sx={{
                            color: '#8cacff',
                            fontSize: 13,
                            fontFamily: 'Poppins',
                            fontWeight: '400',
                            lineHeight: 0.9,
                            textAlign: 'center',
                        }}
                    >
                        {suggestionSubheader}
                    </Typography>
                )}
            </Stack>
        </Card>
    );
}

StationRecoCard.propTypes = {
    suggestionImageSrc: PropTypes.string.isRequired,
    suggestionSubheader: PropTypes.string.isRequired,
    sx: PropTypes.object,
};
